*{margin:0;padding:0}html,body{box-sizing:border-box;height:100%;width:100%}body{background:#fff;font-family:"Noto Sans JP", sans-serif;font-weight:400}.site-wrapper{max-width:1280px;margin:0 auto;padding:0 15px}.header-wrp{display:flex;height:140px;align-items:center}.header-wrp .logo{height:100%;max-height:35px}@media only screen and (min-width: 750px){.medium-up--one-fifth{width:20%}.medium-up--hide{display:none !important}.left-nav{list-style:none}.site-nav ul,.site-footer__menu{padding-left:0;margin-left:0;margin-top:0}.list--inline li{display:inline-block;padding-right:5px;padding-bottom:5px;margin-bottom:0}.social-links a{color:#333}.visually-hidden,.icon__fallback-text,.supports-no-js .featured-blog__post .article__featured-image-wrapper,.supports-no-js .map-section__background-wrapper .js{position:absolute !important;overflow:hidden;clip:rect(0 0 0 0);height:1px;width:1px;margin:-1px;padding:0;border:0}}.listing-title{font-size:70px;line-height:70px;text-align:center;letter-spacing:6.86px}.st-list_container,.st-detail_container{padding-bottom:40px}.st-list_container ul,.st-detail_container ul{display:flex;flex-wrap:wrap;justify-content:space-around}.st-list_container li,.st-detail_container li{margin-top:20px;margin-right:20px;width:25%;height:380px;float:left;overflow:hidden}.st-list_container a,.st-detail_container a{width:100%;height:100%;display:block;position:relative}.st-list_container .st-list_thumb,.st-detail_container .st-list_thumb{position:absolute;top:0;left:0;width:100%;height:auto;z-index:1}.st-list_container .st-list_thumb img,.st-detail_container .st-list_thumb img{width:105%;height:auto}.st-list_container .st-list_desc,.st-detail_container .st-list_desc{background-color:rgba(255,243,247,0.96)}.st-list_container .st-list_desc,.st-detail_container .st-list_desc{padding:20px 0;margin:auto;width:100%;position:absolute;left:0;right:0;bottom:0;transition:0.2s;z-index:2}.st-list_container .st-list_desc p,.st-detail_container .st-list_desc p{font-size:16px;font-weight:400;letter-spacing:2px;color:#000;text-align:center}.st-list_container .st-list_name,.st-detail_container .st-list_name{font-size:24px;letter-spacing:1.5px;transition:0.2s}.st-list_container .st-list_rome,.st-detail_container .st-list_rome{margin-top:5px;font-size:14px;font-size:1.4rem;font-style:italic;letter-spacing:1px;color:#999;transition:0.2s}.st-detail_container{margin:0 auto;width:80%;max-width:1000px;display:flex;justify-content:space-around;flex-wrap:wrap}.st-detail_container li{margin-top:0;width:40%;list-style:none}.st-detail_top_desc{display:flex;flex-wrap:wrap;flex-direction:column;justify-content:space-around}.buttons{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;text-align:center;width:100%;height:100%;margin:0 auto}.buttons .container{align-items:center;display:flex;flex-direction:row;justify-content:space-around;text-align:center;background-color:#fff;padding:40px 0px;width:40%}.btn{letter-spacing:0.1em;cursor:pointer;font-size:14px;font-weight:400;line-height:45px;max-width:160px;position:relative;text-decoration:none;text-transform:uppercase;width:100%}.btn:hover{text-decoration:none}.effect04{--uismLinkDisplay: var(--smLinkDisplay, inline-flex);display:var(--uismLinkDisplay);color:#000;outline:solid 2px #000;position:relative;transition-duration:0.4s;overflow:hidden}.effect04::before,.effect04 span{margin:0 auto;transition-timing-function:cubic-bezier(0.86, 0, 0.07, 1);transition-duration:0.4s}.arrow-btn.back-wrap{margin:0;padding:20px 0px;width:100%;cursor:pointer}.arrow-btn-wrp{display:flex;align-content:center}.arrow-btn-wrp .arrow-text{margin:0 5px}.arrow{display:inline-block;width:15px;height:15px;border-top:2px solid #000;border-right:2px solid #000}.arrow-top{transform:rotate(-45deg)}.arrow-right{transform:rotate(45deg)}.arrow-bottom{transform:rotate(135deg)}.arrow-left{transform:rotate(-135deg)}.arrow-top-narrow{transform:rotate(-45deg) skew(-15deg, -15deg)}.arrow-top-wide{transform:rotate(-45deg) skew(7deg, 7deg)}.arrow-top-left{transform:rotate(-90deg) skew(-10deg, -10deg)}.arrow-top-right{transform:rotate(0) skew(-10deg, -10deg)}.arrow-bottom-left{transform:rotate(180deg) skew(-10deg, -10deg)}.arrow-bottom-right{transform:rotate(90deg) skew(-10deg, -10deg)}

.social-media-list{height:100px}.social-media-list body{margin:0;padding:0;background:#f1f1f1}.social-media-list ul{display:flex;flex-wrap:nowrap}.social-media-list ul li{position:relative;list-style:none;margin:0 2px}.social-media-list ul li::before{content:"";position:absolute;left:0;bottom:-4px;width:40px;height:8px;border-radius:50%;transition:0.5s;opacity:0;-webkit-filter:blur(2px);filter:blur(2px);transform:scale(0.8)}.social-media-list ul li a{width:40px;height:40px;display:block;transition:0.5s}.social-media-list ul li a span{width:100%;height:100%}.social-media-list ul li a span::before{font-family:fontAwesome;text-align:center;line-height:40px;position:absolute;top:0;left:0;width:100%;height:100%;background:#fff;color:#262626;transform-origin:top;transition:transform 0.5s}.social-media-list ul li a span::after{font-family:fontAwesome;text-align:center;line-height:40px;position:absolute;top:0;left:0;width:100%;height:100%;transform-origin:bottom;transition:transform 0.5s;transform:rotateX(90deg) translateY(50%)}.social-media-list ul li:nth-child(1) a span::before,.social-media-list ul li:nth-child(1) a span::after{content:"\f09a"}.social-media-list ul li:nth-child(2) a span::before,.social-media-list ul li:nth-child(2) a span::after{content:"\f099"}.social-media-list ul li:nth-child(3) a span::before,.social-media-list ul li:nth-child(3) a span::after{content:"\f0d5"}.social-media-list ul li:nth-child(4) a span::before,.social-media-list ul li:nth-child(4) a span::after{content:"\f0e1"}.social-media-list ul li:nth-child(1) a span::after{background:#3b5999;color:#fff}.social-media-list ul li:nth-child(2) a span::after{background:#55acce;color:#fff}.social-media-list ul li:nth-child(3) a span::after{background:#dd4b39;color:#fff}.social-media-list ul li:nth-child(4) a span::after{background:#0077b5;color:#fff}

